import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { DATES, InputGroupInline, InputTooltipProps, InputType, WindowComponent, WindowComponentProps, withTooltip } from 'summer';
import { hasEmpresa } from '../../../../../../dependencies/empresaDependency';
import { getDefaultReportBasicValues } from '../../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../../models/dtos/ger/reportViewOptions';
import { CONSOLIDAR_ENUM_VALUES, NORMAL } from '../../../../../../models/enumerated/mcg/consolidarEnum';
import { DT_EMISSAO, OPCAO_DATA_TIPO_ENUM_VALUES } from '../../../../../../models/enumerated/mcg/opcaoDataTipoEnum';
import {
  ANALITICO,
  GRAU_CONTA,
  SINTETICO,
  SINTETICO_ANALITICO_TIPO_VALUE_STRING_ENUM_VALUES,
} from '../../../../../../models/enumerated/mcg/sinteticoAnaliticoTipoEnum';
import { Reducers } from '../../../../../../store/ducks';
import { GlobalState } from '../../../../../../store/ducks/global.duck';
import ReportForm from '../../../../../components/report/ReportForm';
import { BalanceteNormalRelRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from '../balanceteNormalRelRV';

const DEFAULT_VALUES: BalanceteNormalRelRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  acumularDRE: true,
  consolidacao: NORMAL.value,
  exibirFormatacaoValor: true,
  grau: 25,
  marcarContasSinteticasNegrito: false,
  tipo: ANALITICO.value,
  tipoData: DT_EMISSAO.value,
  virtualization: true,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  dtFinalRequired: true,
  dtInicialRequired: true,
  exibirConfiguracaoDadosEmpresa: false,
  exibirImprimirValoresColoridos: true,
  exibirSegmentacaoFolha: true,
};

const BalanceteNormalReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<BalanceteNormalRelRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const [isEnabledGrau, setIsEnabledGrau] = useState(true);
  const [isEnabledGrupo, setIsEnabledGrupo] = useState(true);
  const [isEnabledIsMatriz, setIsEnabledIsMatriz] = useState(false);

  const carregarParametroEmpresa = () => {
    if (globalReducer.globalParameter.empresa.matriz === null) {
      setIsEnabledIsMatriz(true);
      formProps.getValues().matrizFilial = globalReducer.globalParameter.empresa.centralizar;
    }
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        carregarParametroEmpresa();
        adjustTitle();
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().tipo === GRAU_CONTA.value && Number(formProps.getValues().grau) === 0) {
      erros.push('Campo grau não pode ser igual a 0.');
    }

    if (formProps.getValues().quebrarPaginaFimGrupo === true && Number(formProps.getValues().tamanhoGrupo) === 0) {
      erros.push('Campo Quantidade de classes agrupadas não pode ser igual a 0.');
    }

    return erros;
  };

  const acumularMatrizFilial = isEnabledIsMatriz ? (
    <Row>
      <Col>
        <CustomInput type="switch" label="Acumular Matriz e Filiais" name="matrizFilial" id="matrizFilial" innerRef={formProps.register} />
      </Col>
    </Row>
  ) : null;

  const adjustTitle = () => {
    let title;
    title = formProps.getValues().acumularReferencias === true ? 'BALANCETE ACUMULADO' : 'BALANCETE';

    formProps.register({ name: 'acumularDRE' });

    if (formProps.getValues().acumularDRE) {
      title += ' ANUAL';
    }

    if (formProps.getValues().tipo === ANALITICO.value) {
      title += ' ANALÍTICO';
    } else if (formProps.getValues().tipo === SINTETICO.value) {
      title += ' SINTÉTICO';
    } else {
      title += ' - GRAU ' + formProps.getValues().grau;
    }

    if (formProps.getValues().matrizFilial) {
      title += ' - CONSOLIDADO';
    }

    formProps.setValue('title', title, true);
  };

  const enableGrau = () => {
    if (formProps.getValues().tipo === GRAU_CONTA.value) {
      setIsEnabledGrau(false);
    } else {
      setIsEnabledGrau(true);
    }
  };

  const enableTamanhoGrupo = () => {
    if (formProps.getValues().quebrarPaginaFimGrupo === true) {
      setIsEnabledGrupo(false);
    } else {
      setIsEnabledGrupo(true);
    }
  };

  const onChangeTitle = () => {
    enableGrau();
    adjustTitle();
  };

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  return (
    <WindowComponent
      title="Configuração de Balancete Anual"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
    >
      <Row>
        <Col>
          <div className="p-3 mb-2 bg-light text-dark text-center font-weight-bolder">
            Atenção: Ao Gerar o Relatório de Balancete Normal Anual será aberto o encerramento de contas de resultado no processo de geração. Ao final
            do processo ocorrerá novamente o fechamento da forma que estava antes da geração do relatório.
          </div>
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="tipo"
            label="Tipo de Relatório"
            isPrependLabel={true}
            placeholder="Selecione o tipo de relatório"
            formProps={{ ...formProps, validation: { required: true } }}
            onChange={onChangeTitle}
            inputDropDownProps={{
              optionsProvider: SINTETICO_ANALITICO_TIPO_VALUE_STRING_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
        <Col>
          <InputGroupInline
            id="grau"
            label="Grau"
            isPrependLabel={true}
            placeholder="digite o grau da conta"
            formProps={{ ...formProps, validation: { max: 2147483647 } }}
            disabled={isEnabledGrau}
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Acumular Referências"
            onChange={onChangeTitle}
            name="acumularReferencias"
            id="acumularReferencias"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Remover espaçamento dos títulos das contas"
            name="alinharEsquerda"
            id="idalinharEsquerda"
            innerRef={formProps.register}
          />
          {withTooltip(
            propToolTip('Caso essa opção seja selecionada os títulos das contas ficarão sem os espaçamentos e alinhados á esquerda'),
            'alinharEsquerda'
          )}
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Exibir referência no formato dd/mm/aaaa"
            name="exibirReferenciaFormatoData"
            id="idexibirReferenciaFormatoData"
            innerRef={formProps.register}
          />
          {withTooltip(
            propToolTip('Caso essa opção seja selecionada a referência exibida no cabeçalho do relatório será no formato dd/mm/yyyy'),
            'exibirReferenciaFormatoData'
          )}
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Exibir contas sintéticas em negrito"
            name="marcarContasSinteticasNegrito"
            id="idmarcarContasSinteticasNegrito"
            innerRef={formProps.register}
          />
          {withTooltip(
            propToolTip('Caso essa opção seja selecionada as contas sintéticas serão exibidas em negrito.'),
            'marcarContasSinteticasNegrito'
          )}
        </Col>
      </Row>
      {acumularMatrizFilial}
      <Row className="mt-3">
        <Col>
          <InputGroupInline
            id="tipoData"
            label="Data"
            isPrependLabel={true}
            placeholder="Selecione o tipo de data"
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: OPCAO_DATA_TIPO_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <hr />
      <Row className="mb-3">
        <Col>
          <CustomInput
            type="switch"
            label="Quebrar Página ao Fim de cada Classe"
            name="quebrarPaginaFimGrupo"
            id="idquebrarPaginaFimGrupo"
            onChange={enableTamanhoGrupo}
            innerRef={formProps.register}
          />
          {withTooltip(
            propToolTip('Quebrar página ao fim de cada classes do plano de contas - Ativo, Passivo, Receitas, Despesas.'),
            'quebrarPaginaFimGrupo'
          )}
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="tamanhoGrupo"
            label="Quantidade de Classes Agrupadas"
            isPrependLabel={true}
            placeholder="digite a quantidade de classes agrupadas"
            formProps={{ ...formProps, validation: { max: 2147483647 } }}
            disabled={isEnabledGrupo}
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <hr />
      <Row>
        <Col>
          <InputGroupInline
            id="consolidacao"
            label="Consolidação"
            isPrependLabel={true}
            placeholder="Selecione uma consolidação"
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: CONSOLIDAR_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtAssinatura"
            label="Data Assinatura"
            isPrependLabel={true}
            placeholder="digite a data de assinatura"
            formProps={formProps}
            type={InputType.DATE}
            tooltipProps={propToolTip('Caso este campo esteja vazio a data de assinatura será o último dia da referência.')}
          />
        </Col>
      </Row>
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={{ ...DEFAULT_VALUES, ...{ title: 'BALANCETE ANUAL ANALÍTICO' } }}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      />
    </WindowComponent>
  );
};

export default BalanceteNormalReportView;
